import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup , Validators } from '@angular/forms';
import { from } from 'rxjs';
import {UserdataService} from '../userdata.service'
import { Observable } from 'rxjs';
import {Router} from '@angular/router'

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
  allformgroup:any ;
  sginupmessage:any ;
  sucsessrespose:any = false

  constructor(private _UserdataService:UserdataService , private _Router:Router) {
    this.allformgroup = new FormGroup({
      'first_name' : new FormControl( null , [Validators.required , Validators.minLength(3), Validators.maxLength(10)]),
      'last_name' : new FormControl(null , [Validators.required , Validators.minLength(3), Validators.maxLength(10)]),
      'age' : new FormControl( null , [Validators.required , Validators.min(20),Validators.max(40)]),
      'email' : new FormControl( null , [Validators.required , Validators.email]),
      'password' : new FormControl(null , [Validators.required , Validators.pattern('^[A-Z][a-zA-Z]+$')])
    })
   }
   collectdata(data){
    this._UserdataService.senddatatoback(data.value).subscribe((response)=>{
      if (response.message =="success"){
        this.sucsessrespose = true
        this.sginupmessage = response.message+" You will directed now to Login page"
        setTimeout(()=>{this._Router.navigate(['/login'])} , 3000)
      }else{
        this.sucsessrespose = true
        this.sginupmessage = response.errors.email.message
      }
    })
  }

  ngOnInit(): void {
  }

}
