import { Component, OnInit } from '@angular/core';
import {FormControl , FormGroup , Validators} from '@angular/forms'
import {UserdataService} from '../userdata.service'
import {Router} from '@angular/router'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit  {
  loginnow:any ;
  alertlogin:any = false ;
  message:any ;
  constructor(private _UserdataService:UserdataService , public _Router:Router) { 
    this.loginnow = new FormGroup({
      'email' : new FormControl(null , [Validators.required , Validators.email] ),
      'password' : new FormControl(null , [Validators.required , Validators.pattern('^[A-Z][a-zA-Z]+$')])
    })
  }

  collectlogindata(loginnow){
    this._UserdataService.sendlogindata(loginnow.value).subscribe((data)=>{
      if (data.message == "success"){
        this._UserdataService.getDataForOneUser(data.user.first_name,data.user.last_name,data.user.age,data.token)
        this.alertlogin = true
        this.message = data.message
        setTimeout(()=>{this._Router.navigate(['/home'])}, 2000)
      }else{
        this.alertlogin = true
        this.message = data.message
      }
    })
  }


  ngOnInit(): void {
  }



}
