import { Component, OnInit } from '@angular/core';
import {GetmoviesService} from '../getmovies.service'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  moviearry : object ;
  imgurl: string = "https://image.tmdb.org/t/p/w500"
  constructor(public _GetmoviesService:GetmoviesService) {
    _GetmoviesService.getmoviesarry().subscribe((data)=>{
      this.moviearry = data.results
    })
   }

  ngOnInit(): void {
  }

}
