import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http'

import { from , Observable } from 'rxjs';



@Injectable({
  providedIn: 'root'
})
export class UserdataService  {
  firstname : string ;
  lastname : string ;
  age : string ;
  token : string ;

  constructor(private _HttpClient:HttpClient ){}

  senddatatoback(userdata:object):Observable<any>
  {
    return this._HttpClient.post("https://routeegypt.herokuapp.com/signup", userdata)
  }

  sendlogindata(logindata:object):Observable<any>{
   return this._HttpClient.post("https://routeegypt.herokuapp.com/signin" , logindata)
  }

  getDataForOneUser(fname:string,lasname:string,age:string,token:string,){
    this.firstname = fname ;
    this.lastname = lasname ;
    this.age = age ;
    this.token = token ;

  }
   
}
